import "./nav.module.css";
import Link from "next/link";

export const Nav = () => {
  return (
    <>
      <div>This is my Nav</div>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "space-around",
          height: "80px",
          background: "aquamarine",
          alignItems: "center"
        }}
      >
        <Link href="/">
          <a>Home</a>
        </Link>
        <Link href="/about">
          <a>About</a>
        </Link>
        <Link href="/contact">
          <a>Contact</a>
        </Link>
        <div>
          <form>
            <label>
              Login:
              <input type="text" name="name" />
            </label>
            <label>
              Password:
              <input type="password" name="password" />
            </label>
            <input type="submit" value="Submit" />
          </form>
        </div>
      </div>
    </>
  );
};
